.task-container {
    background-color: #444;
    margin: 8px 0;
    padding: 15px 20px;
    display: flex;
    border-radius: 5px;
    color: #eee;
    justify-content: space-between;
    align-items: center;
}

.task-title {
    cursor: pointer;
}

.buttons-container {
    min-width: 30px;
    display: flex;
    justify-content: flex-end;
}

.remove-task-button,
.see-task-detail-button {
    background-color: #444;
    border: none;
    font-size: 24px;
    color: #1976d2;
    cursor: pointer;
    margin: 0 5px;
}

.add-task-container {
    margin: 15px 0;
    width: 100%;
    display: flex;
}

.add-task-input {
    width: 100%;
}

.add-task-button-container {
    margin-left: 10px;
    flex: 1 1;
}

.add-button {
    height: 40px;
    width: 115px;
}
.task-details-container {
    background-color: #444;
    margin: 8px 0;
    padding: 15px 20px;
    display: flex;
    border-radius: 5px;
    color: #eee;
    flex-direction: column;
    height: 60px;
}

.task-details-container h2 {
    font-size: 1.2rem;
    color: #eee;
}

.back-button-container {
    width: 100%;
    height: 40px;
    margin: 15px 0;
    display: flex;
    justify-content: flex-end;
}

.back-button {
    height: 40px;
    width: 115px;
}

.task-input-detail {
    height: 100px;
    width: 100%;
    padding: 10px 10px;
    border-radius: 5px;
    border: none;
    flex: 2 1;
    background-color: #444;
    color: #eee;
    font-size: 1rem;
    box-sizing: border-box;
    resize: none;
    text-transform: capitalize;
}
.container {
    max-width: 500px;
    margin: 30px auto;
    overflow: auto;
    min-height: 300px;
    border: 2px solid #1976d2;
    padding: 30px;
    border-radius: 10px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(223, 223, 223);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

